@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');
.backdrop{
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 150px;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0,0,0,0.5);
    z-index: 999;
}

.modal-container{
    width: 400px;
    min-height: 300px;
    margin: 0 20px;
    padding: 20px;
    background: white;
    border-radius: 10px;
    text-align: center;
    border: 1px solid black;
}

.modal-header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #dee2e6;
    min-height: 50px;
}

.modal-close-btn svg{
    cursor: pointer;
}

.modal-body{
    display: flex;
    justify-content: center;
    width: 100%;
    min-height: 300px;
}

.modal-content{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 100%;
    padding: 5px 15px;
}

.modal-msg{
    height: 100px;
    width: 50%;
    position: relative;
    display: flex;
    justify-content: space-between;
    color: black;
    cursor: pointer;
}

.modal-content i{
    font-family: 'Bebas Neue', cursive;
    font-size: 3rem;
}

.modal-msg span{
    position: relative;
    filter: blur(6px);
    padding: 0 18px;
    transition: .6s;
    height: 50px;
}

.modal-msg span:hover{
    filter: blur(0px);
    transition: 0s;
}

.modal-msg i {
    position: absolute;
    inset: 0;
    background: transparent;
}


@media (max-width: 750px){

    .modal-body{
        flex-direction:column;
        align-items: center;
        justify-content: space-around;
        height: 90%;
    }
}