.cards {
    display: flex;
    margin-top: -55px;
    width: auto;
    min-height: auto;
    border: 1px solid gainsboro;
    border-radius: 10px;
    box-shadow: 0 5px 5px 0 rgb(233 240 243 / 50%);
    background-color: white;
}

.col {
    width: 270px;
    height: 400px;
    padding-bottom: 10px;
    display: flex;
    flex-direction: column;
    text-align: center;
}

.col p {
    margin: 15px;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 1px;
    font-weight: 600;
    font-size: .8rem;
}

.cards-container .icon-container {
    width: 100%;
    height: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.cards-container > div:nth-child(2) {
    border-left: 1px solid #E6ECF8;
    border-right: 1px solid #E6ECF8;
}

.dev-tools-container {
    height: auto;
    display: flex;
    justify-content: space-evenly;
}

.cards-container ul {
    list-style: none;
}

.cards-container li {
    margin: 10px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
}

.cards-container li:hover {
    color: #7510F7;
    transition: all .3s ease-in-out;
}

.cards-container a {
    text-decoration: none;
}

.col:last-child > p:last-child {
    border-top: 1px solid #E6ECF8;
    padding-top: 10px;
}

.cards-container strong {
    font-size: 1.2rem;
}

@media (max-width: 700px) {
    .cards {
        flex-direction: column;
    }

    .cards-container > div:nth-child(2) {
        border-left: 0 solid #E6ECF8;
        border-right: 0 solid #E6ECF8;
    }

    .cards-container .col {
        border-bottom: 1px solid #E6ECF8;
    }
}